:root {
    --bg1: #0f121a;
    --bg2: #1a2030;
    --accent: #6ee7f0;
    --accent-2: #a78bfa;
    --card-front: #111827;
    --card-back: #0b1220;
    --text: #e5e7eb;
    --muted: #9ca3af;
}

.minigame{
    display: grid;
    justify-content: center;
}

.mini-wrapper {
    width: 400px;
    display: grid;
    gap: 10px;
    color: var(--text);
    font-family: "Segoe UI", sans-serif;
}

/* Top bar */
.mini-topbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 6px 10px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.badge {
    font-weight: 600;
    color: #000;
}

.mini-btn {
    background: linear-gradient(90deg, var(--accent), var(--accent-2));
    border: none;
    padding: 6px 12px;
    border-radius: 8px;
    color: #000;
    font-weight: 700;
    cursor: pointer;
    transition: 0.2s;
}

.mini-btn:hover {
    transform: translateY(-2px);
}

/* Board */
.mini-board {
    width: 400px;
    height: 400px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 8px;
    background: linear-gradient(120deg, var(--bg1), var(--bg2));
    padding: 10px;
    border-radius: 14px;
    position: relative;
}

/* Card */
.card {
    position: relative;
    cursor: pointer;
    perspective: 600px;
}

.inner {
    width: 100%;
    height: 100%;
    transition: 0.4s;
    transform-style: preserve-3d;
    border-radius: 12px;
}

.card.flipped .inner {
    transform: rotateY(180deg);
}

.front,
.back {
    position: absolute;
    inset: 0;
    border-radius: 12px;
    display: grid;
    place-items: center;
    font-size: 34px;
    backface-visibility: hidden;
}

.front {
    background: #fff;
    transform: rotateY(180deg);
}

.back {
    background: #181f2e;
    color: #fff;
}

/* Win popup */
.win-overlay {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.6);
    display: grid;
    place-items: center;
    border-radius: 14px;
}

.win-card {
    background: #121826;
    padding: 16px 20px;
    border-radius: 12px;
    text-align: center;
    border: 1px solid rgba(255, 255, 255, 0.2);
}

.mini-board {
    display: grid;
    gap: 8px;
}
